<template>
  <v-container
    id="register"
    fluid
    pt-4
  >
    <v-layout
      justify-center
      wrap
    >
      <!-- Error message if any -->
      <v-flex
        xs12
      >
        <errorMessageDialog />
      </v-flex>
      <!-- Page title -->
      <v-flex
        xs12
        class="register-title text-xs-center py-4"
      >
        <h1>{{ pageTitle }}</h1>
      </v-flex>
      <v-flex
        xs10
        sm8
        md7
      >
        <v-form
          ref="form"
          v-model="valid"
          lazy-validation
        >
          <v-text-field
            v-model="userNameFormValue"
            :rules="nameRules"
            :counter="10"
            label="Name"
            color="purple accent-4"
            autocomplete="off"
          />
          <v-text-field
            v-model="email"
            :rules="emailRules"
            label="E-mail"
            autocomplete="email"
            color="purple accent-4"
            required
          />
          <v-layout>
            <v-flex xs11>
              <v-text-field
                v-model="password"
                :type="showPass ? 'text' : 'password'"
                :rules="passwordRules"
                label="Password"
                autocomplete="new-password"
                color="purple accent-4"
                required
                hint="At least 6 characters"
              />
            </v-flex>
            <v-flex
              d-flex
              xs1
              align-center
              align-content-center
              justify-center
              class="text-xs-center"
              @click="showPass = !showPass"
            >
              <showPassIcon
                class="input-icon"
                :class="{ showPass: showPass }"
              />
            </v-flex>
          </v-layout>
          <v-layout>
            <v-flex>
              <v-text-field
                v-model="confirmPassword"
                :type="showConfPass ? 'text' : 'password'"
                :rules="[comparePasswords]"
                label="Confirm password"
                autocomplete="off"
                color="purple accent-4"
                required
              />
            </v-flex>
            <v-flex
              d-flex
              xs1
              align-center
              align-content-center
              justify-center
              class="text-xs-center"
              @click="showConfPass = !showConfPass"
            >
              <showPassIcon
                class="input-icon"
                :class="{ showConfPass: showConfPass }"
              />
            </v-flex>
          </v-layout>
        </v-form>
      </v-flex>
      <v-flex
        xs10
        sm8
        md7
      >
        <v-layout
          justify-center
          class="buttons text-xs-center py-4"
        >
          <v-flex
            xs6
            d-flex
          >
            <v-btn
              :disabled="!valid"
              :loading="registering"
              class="white--text button"
              outline
              color="orange"
              @click.prevent="signUp"
            >
              register
            </v-btn>
          </v-flex>
          <v-flex
            xs6
            d-flex
          >
            <v-btn
              :disabled="valid"
              class="button white--text"
              outline
              color="purple darken-1"
              @click="clear"
            >
              clear
            </v-btn>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import errorMessageDialog from '../components/ErrorMessage.vue'
import showPassIcon from '../assets/icons/baseline-remove_red_eye-24px.svg'
import db from '../firebase/firebaseInit'
import firebase from 'firebase/app'
import 'firebase/auth'

export default {
  name: `Register`,
  components: {
    errorMessageDialog,
    showPassIcon
  },
  data: () => ({
    pageTitle: `Register`,
    registering: false,
    errorMessage: ``,
    valid: true,
    usersCollRef: `users`,
    userNameFormValue: ``,
    nameRules: [
      (v) => (!v || v.length <= 10) || `Name must be less than 10 characters`
    ],
    email: ``,
    emailRules: [
      (v) => !!v || `E-mail is required`,
      (v) => /^([a-zA-Z0-9_\-\\.]+)@([a-zA-Z0-9_\-\\.]+)\.([a-zA-Z]{2,5})$/.test(v) || `E-mail must be valid`
    ],
    password: ``,
    showPass: false,
    passwordRules: [
      (v) => !!v || `Password is required`,
      (v) => (v && v.length <= 12) || `Password must be less than 12 characters`
    ],
    confirmPassword: ``,
    showConfPass: false
  }),
  computed: {
    comparePasswords() {
      return this.password !== this.confirmPassword ? `Passwords do not match` : true
    }
    // comparePasswords: () => this.password !== this.confirmPassword ? `Passwords do not match` : true
    /*
    comparePasswords() {
      return this.password !== this.confirmPassword ? `Passwords do not match` : true
    }
    */
  },
  mounted() {
    this.$nextTick(() => {
      console.log(`Register page mounted.`)
    })
  },
  methods: {
    signUp() {
      this.errorMessage = `` // clear error message?
      if (this.email && this.password) {
        this.registering = true
        firebase.auth().createUserWithEmailAndPassword(this.email, this.password)
          .then(response => {
            // add new user data to db
            if (this.userNameFormValue === ``) { // it is optional, privacy meh
              this.userNameFormValue = `Anonymous` // get default name??
            }
            localStorage.setItem(`userUid`, response.user.uid)
            localStorage.setItem(`userName`, this.userNameFormValue)
            const newUserData = {
              name: localStorage.getItem(`userName`),
              uid: response.user.uid,
              email: response.user.email,
              hiScore: localStorage.getItem(`highestScore`),
              resultsArray: localStorage.getItem(`lastScoresArray`),
              schoolResultsArray: localStorage.getItem(`schoolScores`),
              diceValuesFavs: localStorage.getItem(`diceValuesFavs`),
              combinationsFavs: localStorage.getItem(`combinationsFavs`)
            }
            this.addNewUser(newUserData)
            console.log(`User added ${response.user.email}`)
            this.verifyUserEmail()
            // some message with button to the game view needed
            this.$router.push(`/game`)
          })
          .catch(err => {
            console.log(err.message)
            this.errorMessage = err.message
            this.registering = false
          })
      }
    },
    addNewUser(userData) {
      console.log(`Adding user`)
      // Add a new document with a generated id.
      const newUserRef = db.collection(this.usersCollRef).doc()
      newUserRef.set(userData)
      console.log(`Document successfully written!`)
    }, // end of user adding
    verifyUserEmail() {
      const user = firebase.auth().currentUser
      user.sendEmailVerification().then(() => {
        console.log(`Email sent`)
      }).catch(function(error) {
        console.log(`Error! ${error}`)
      })
    },
    clear() {
      this.$refs.form.reset()
      this.showPass = false
      this.showConfPass = false
    }
  }
}
</script>

<style lang="scss" scoped>

@import '../assets/scss/index.scss';

* {
  font-family: $text-font;
}

#register {
  height: 100%;
}

.button {
  line-height: 2em;
}

.showPass, .showConfPass {
  color: $color-primary-0;
}

</style>
